import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  getDataNoTimeOut,
    formatDate,
    PanelBody,
  moment,
  ToastNotification,
  localStorageencryp,
} from "../../../../components/helpers/library";
import HeadLaporanServiceMasuk from "./HeadLaporanServiceMasuk";

class LaporanServiceMasuk extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      LaporanServiceMasuk: [],
      export: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
    };
    this.setState({
      isLoading: true,
    });
    getDataNoTimeOut(
      "service/report-service-masuk?tgl_from=" +
        data_post.tgl_awal +
        "&tgl_to=" +
        data_post.tgl_akhir
    )
      .then((res) => {
        // console.log(res);
        if (res.data.length === 0) {
          ToastNotification("info", "Laporan Service Masuk Kosong");
          this.setState({
            export: false,
            isLoading: false,
          });
       
          return false;
        }
        ToastNotification("success", "Laporan Service Masuk Tersedia");
        localStorageencryp("tgl_laporan", JSON.stringify(data_post));
        this.setState({
          LaporanServiceMasuk: res.data,
          isLoading: false,
          export: true,
        });
      })
      .catch((err) => {
        ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
        this.setState({
          export: false,
          isLoading: false,
        });
      });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Service Masuk</li>
        </ol>
        <h1 className="page-header">Laporan Service Masuk </h1>
        <Panel>
          <PanelHeader>Laporan Service Masuk</PanelHeader>
          <br />
          {/* Blank Page */}
          <PanelBody>
            <HeadLaporanServiceMasuk
              export={this.state.export}
              isLoading={this.state.isLoading}
              LaporanServiceMasuk={this.state.LaporanServiceMasuk}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>

          <br />
          {/* End Tambah Blank Page  */}
        </Panel>
      </div>
    );
  }
}

LaporanServiceMasuk = reduxForm({
  form: "LaporanServiceMasuk",
  enableReinitialize: true,
})(LaporanServiceMasuk);
export default connect(null)(LaporanServiceMasuk);
